import React from "react";
import {Container, Row, Col} from "react-bootstrap";
import { stats } from "../data/relics";

const Stats = (props) => {

    return (
        <Container fluid>
            <Row><Col className="stats" style={{textAlign: props.align, fontWeight: 'bold'}}>{'Total Shuriken ATK ' + props.derivedShuriken}</Col></Row>
            <Row><Col className="stats" style={{textAlign: props.align, fontWeight: 'bold'}}>{'Total Weapon ATK ' + props.derivedWeapon}</Col></Row>
            <Row><Col className="stats" style={{textAlign: props.align, fontWeight: 'bold'}}>{'Total Ninjutsu ATK ' + props.derivedNinjutsu}</Col></Row>
            <Row><Col className="stats">&nbsp;</Col></Row>
            {
                Object.keys(props.combinedStats).sort().map((key, index) => {
                    let prefix = stats[key].prefix;
                    let suffix = stats[key].suffix;
                    let value = props.combinedStats[key];
                    let text = prefix + ' ' + value + ' ' + suffix;
                    return <Row key={index}><Col className="stats" style={{textAlign: props.align}}>{text}</Col></Row>
                })
            }
        </Container>
    );
};

export default Stats;